import { RestaurentThemeComponent } from './../restaurent-theme/restaurent-theme.component';
import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA, MatDialog } from '@angular/material/dialog';
import { FormGroup, FormBuilder } from '@angular/forms';
import { Observable } from 'rxjs';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { RestaurentCartComponent } from '../restaurent-cart/restaurent-cart.component';

@Component({
  selector: 'app-restaurent-product',
  templateUrl: './restaurent-product.component.html',
  styleUrls: ['./restaurent-product.component.scss']
})
export class RestaurentProductComponent implements OnInit {
 token;
  image;
  urls: any;
  mrp;
  varPrice: string;
  varValue:boolean
  sellingprice:Observable<any>;
  name: any;
  variance=[];
  // formVar:FormGroup
  constructor(private dialogRef: MatDialogRef<RestaurentThemeComponent>,  @Inject(MAT_DIALOG_DATA) public data: any,
  private http:HttpClient,private route:Router,public dialog: MatDialog
  ) {}

  ngOnInit(): void {
    this.image=this.data.dataKey
    this.variance=this.data.dataKey.arrVariance
    this.urls=this.data.dataKey.strImageUrl
    this.mrp=this.data.dataKey.dblMRP
    this.sellingprice=this.data.dataKey.dblSellingPrice
  }
 
  close(){
    this.dialogRef.close()
  }

  onClickImage(id){
    this.urls=id
  }

  radioChange(e){
   this.sellingprice = e.dblSellingPrice
   this.mrp=e.dblMRP
   this.name =e.strName
   console.log(this.sellingprice, this.mrp)
  }

addCart(){
  this.token= localStorage.getItem('token')
  let headers = new HttpHeaders({ 'strAppInfo': 'big','strToken':this.token })
  let params={
    "strProductId":this.image._id,
    "dblQty":1,
    "objExtras":{
      "strVariance":this.name,
      "dblPrice":this.sellingprice,

    }
}
return this.http.post('http://15.207.68.165:4002/cart/create_cart',params,{headers:headers}).subscribe((body)=>{
 
  console.log(body);
  //window.location.reload()
  this.close()
})
}
buyNow(){
  this.addCartbuynow()
  
    const dialogRef = this.dialog.open(RestaurentCartComponent,{
      width:'90%',
      height:'90%'
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
    });
  this.close()
    
}

addCartbuynow(){
  this.token= localStorage.getItem('token')
  let headers = new HttpHeaders({ 'strAppInfo': 'big','strToken':this.token })
  let params={
    "strProductId":this.image._id,
    "dblQty":1,
    "objExtras":{
      "strVariance":this.name,
      "dblPrice":this.sellingprice,

    }
}
return this.http.post('http://15.207.68.165:4002/cart/create_cart',params,{headers:headers}).subscribe((body)=>{
 
  console.log(body);
  // window.location.reload()
  // this.close()
})
}
}
