import { RestaurentCartComponent } from './../restaurent-cart/restaurent-cart.component';
import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { MatDialogRef, MatDialog } from '@angular/material/dialog';
import { environment } from 'src/environments/environment.prod';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { RestaurentProductComponent } from '../restaurent-product/restaurent-product.component';
import { Observable } from 'rxjs';
const token=localStorage.getItem('token')
const head = new HttpHeaders({ 'Content-Type': 'application/json','strAppInfo': 'big' ,'strToken':token ||""})
@Component({
  selector: 'app-restaurent-theme',
  templateUrl: './restaurent-theme.component.html',
  styleUrls: ['./restaurent-theme.component.scss']
})
export class RestaurentThemeComponent implements OnInit {
  @ViewChild('widgetsContent') widgetsContent: ElementRef;
  shop: Observable<any[]>;
  category: Observable<any[]>;
  products:Observable<any[]>;
  token;
  productVar;
  constructor(public dialog: MatDialog,private http:HttpClient) { }


  ngOnInit(): void {
    this.getProduct()
    this.getTheme()
    this.getCategory()
    
   
  }
  getTheme(){
    let param={
      "strName":"mdl_home"
    }
    return this.http.post(environment.apiUrl+'common/get_module_settings',param,{headers:head}).subscribe((body)=>{
      console.log(body)
      this.shop=body['objShop']
      console.log(this.shop);
      
    })
  }
  getCategory(){
    let param={
      "arrCollection":[
       
        {"strCollection":"cln_category"}
       ]
    }
    return this.http.post(environment.apiUrl+'master/get_master',param,{headers:head}).subscribe((cat)=>{
      // console.log(cat);
      this.category=cat['cln_category']
      console.log(this.category);
      
      

    })
  }
  private product:any={
    "intLimit":Number,
    "intPageNo":Number,
        "strSort":"",
        "strSortActive":"ASC",
        "arrCategory":[],
        "arrBrands":[],
        "strSearch":null,
      

  }
  valueCat(p){
    console.log(p)
    this.product.arrCategory=[p]
    this.getProduct()
  }
  valueChanged(e) {
    console.log(e);
    
    this.product.strSearch = e
    console.log(this.product.strSearch)
    this.getProduct()
  }

  getProduct(){

    return this.http.post('http://15.207.68.165:4002/product/get_product',this.product,{headers:head}).subscribe((pro)=>{
 console.log(pro);
    this.token=pro['strToken']
    console.log(this.token);
    localStorage.setItem('token',this.token)
    
      this.products=pro['arrList'],
      this.productVar=pro
      console.log(this.productVar);
      
      console.log(this.products);
      
      
    })
  }
  openCart() {
    const dialogRef = this.dialog.open(RestaurentCartComponent,{
      width:'60%',
      height:'90%'
    });

    dialogRef.afterClosed().subscribe(result => {
      this.getProduct()
      console.log(`Dialog result: ${result}`);
    });
  }
  openProduct(id) {
    const dialogRef = this.dialog.open(RestaurentProductComponent,{
      width:'90%',
      height:'auto+20%',
      data: {
        dataKey: id
      }
    });
    console.log(id);
    
    dialogRef.afterClosed().subscribe(result => {
      this.getProduct()
      console.log(`Dialog result: ${result}`);
    });
  }
  scrollLeft(){
    this.widgetsContent.nativeElement.scrollLeft -= 250;
  }

  scrollRight(){
    this.widgetsContent.nativeElement.scrollLeft += 250;
  }
  createCart(id){

    let tok=localStorage.getItem('token')
    console.log(tok);
    
    let headers = new HttpHeaders({ 'strAppInfo': 'big','strToken':tok })
    let param={
      "strProductId":id._id,
          "dblQty":1,
          "objExtras":{
            "strVariance":id.arrVariance[0].strName,
            "dblPrice":id.arrVariance[0].dblPrice
          }
    }
    console.log(param);
    
    return this.http.post('http://15.207.68.165:4002/cart/create_cart',param,{headers:headers}).subscribe((cartProduct)=>{
      console.log(cartProduct);
      
    })
  
  }
}
